import Vue from 'vue'
// Vuex
import Vuex from 'vuex'
// VuexVue
Vue.use(Vuex)

// VuexStore
export default new Vuex.Store({
  // 
  state: {
    // 
    nickName: '',
    // 
    cartCount: 0
  },
  // 
  mutations: {
    // nickName
    updateUserInfo (state, nickName) {
      // nickNamenickName
      state.nickName = nickName
    },
    // , cartCount
    updateCartCount (state, cartCount) {
      // 
      state.cartCount += Number.parseInt(cartCount)
    },
    // 
    clearCartCount (state) {
      // 0
      state.cartCount = 0
    }
  }
})



